


'use strict';

import Easing from './../Easing/Easing.web'
import Interpolation from './Interpolation.web'
import React from 'react'
import SpringConfig from './SpringConfig'
import {flatten,fix} from './../StyleSheet/StyleSheet.web'


class Animated {
    __attach() {}
    __detach() {}
    __getValue() {}
    __getAnimatedValue() { return this.__getValue(); }
    __addChild(child) {}
    __removeChild(child) {}
    __getChildren(){ return []; }
}


class Animation {
    __active;
    __isInteraction;
    __onEnd;
    start(
        fromValue,
        onUpdate,
        onEnd,
        previousAnimation,
    ){}
    stop(){}
    // Helper function for subclasses to make sure onEnd is only called once.
    __debouncedOnEnd(result) {
        let onEnd = this.__onEnd;
        this.__onEnd = null;
        onEnd && onEnd(result);
    }
}

class AnimatedWithChildren extends Animated {
    _children;

    constructor() {
        super();
        this._children = [];
    }

    __addChild(child) {
        if (this._children.length === 0) {
            this.__attach();
        }
        this._children.push(child);
    }

    __removeChild(child) {
        let index = this._children.indexOf(child);
        if (index === -1) {
            console.warn('Trying to remove a child that doesn\'t exist');
            return;
        }
        this._children.splice(index, 1);
        if (this._children.length === 0) {
            this.__detach();
        }
    }

    __getChildren() {
        return this._children;
    }
}

/**
 * Animated works by building a directed acyclic graph of dependencies
 * transparently when you render your Animated components.
 *
 *               new Animated.Value(0)
 *     .interpolate()        .interpolate()    new Animated.Value(1)
 *         opacity               translateY      scale
 *          style                         transform
 *         View#234                         style
 *                                         View#123
 *
 * A) Top Down phase
 * When an Animated.Value is updated, we recursively go down through this
 * graph in order to find leaf nodes: the views that we flag as needing
 * an update.
 *
 * B) Bottom Up phase
 * When a view is flagged as needing an update, we recursively go back up
 * in order to build the new value that it needs. The reason why we need
 * this two-phases process is to deal with composite props such as
 * transform which can receive values from multiple parents.
 */
function _flush(rootNode) {
    let animatedStyles = new Set();
    function findAnimatedStyles(node) {
        if (typeof node.update === 'function') {
            animatedStyles.add(node);
        } else {
            node.__getChildren().forEach(findAnimatedStyles);
        }
    }
    findAnimatedStyles(rootNode);
    animatedStyles.forEach(animatedStyle => animatedStyle.update());
}

let easeInOut = Easing.inOut(Easing.ease);

class TimingAnimation extends Animation {
    _startTime;
    _fromValue;
    _toValue;
    _duration;
    _delay;
    _easing;
    _onUpdate;
    _animationFrame;
    _timeout;

    constructor(
        config,
    ) {
        super();
        this._toValue = config.toValue;
        this._easing = config.easing || easeInOut;
        this._duration = config.duration !== undefined ? config.duration : 500;
        this._delay = config.delay || 0;
        this.__isInteraction = config.isInteraction !== undefined ? config.isInteraction : true;
    }

    start(
        fromValue,
        onUpdate,
        onEnd
    ){
        this.__active = true;
        this._fromValue = fromValue;
        this._onUpdate = onUpdate;
        this.__onEnd = onEnd;

        let start = () => {
            if (this._duration === 0) {
                this._onUpdate(this._toValue);
                this.__debouncedOnEnd({finished: true});
            } else {
                this._startTime = Date.now();
                this._animationFrame = requestAnimationFrame(this.onUpdate.bind(this));
            }
        };
        if (this._delay) {
            this._timeout = setTimeout(start, this._delay);
        } else {
            start();
        }
    }

    onUpdate() {
        let now = Date.now();
        if (now >= this._startTime + this._duration) {
            if (this._duration === 0) {
                this._onUpdate(this._toValue);
            } else {
                this._onUpdate(
                    this._fromValue + this._easing(1) * (this._toValue - this._fromValue)
                );
            }
            this.__debouncedOnEnd({finished: true});
            return;
        }

        this._onUpdate(
            this._fromValue +
            this._easing((now - this._startTime) / this._duration) *
            (this._toValue - this._fromValue)
        );
        if (this.__active) {
            this._animationFrame = requestAnimationFrame(this.onUpdate.bind(this));
        }
    }

    stop() {
        this.__active = false;
        clearTimeout(this._timeout);
        global.cancelAnimationFrame(this._animationFrame);
        this.__debouncedOnEnd({finished: false});
    }
}


class DecayAnimation extends Animation {
    _startTime;
    _lastValue;
    _fromValue;
    _deceleration;
    _velocity;
    _onUpdate;
    _animationFrame;

    constructor(
        config
    ) {
        super();
        this._deceleration = config.deceleration || 0.998;
        this._velocity = config.velocity;
        this.__isInteraction = config.isInteraction !== undefined ? config.isInteraction : true;
    }

    start(fromValue,onUpdate,onEnd) {
        this.__active = true;
        this._lastValue = fromValue;
        this._fromValue = fromValue;
        this._onUpdate = onUpdate;
        this.__onEnd = onEnd;
        this._startTime = Date.now();
        this._animationFrame = requestAnimationFrame(this.onUpdate.bind(this));
    }

    onUpdate() {
        let now = Date.now();

        let value = this._fromValue +
            (this._velocity / (1 - this._deceleration)) *
            (1 - Math.exp(-(1 - this._deceleration) * (now - this._startTime)));

        this._onUpdate(value);

        if (Math.abs(this._lastValue - value) < 0.1) {
            this.__debouncedOnEnd({finished: true});
            return;
        }

        this._lastValue = value;
        if (this.__active) {
            this._animationFrame = requestAnimationFrame(this.onUpdate.bind(this));
        }
    }

    stop() {
        this.__active = false;
        global.cancelAnimationFrame(this._animationFrame);
        this.__debouncedOnEnd({finished: false});
    }
}



function withDefault(value, defaultValue) {
    if (value === undefined || value === null) {
        return defaultValue;
    }
    return value;
}

class SpringAnimation extends Animation {
    _overshootClamping;
    _restDisplacementThreshold;
    _restSpeedThreshold;
    _initialVelocity;
    _lastVelocity;
    _startPosition;
    _lastPosition;
    _fromValue;
    _toValue;
    _tension;
    _friction;
    _lastTime;
    _onUpdate;
    _animationFrame;

    constructor(
        config
    ) {
        super();

        this._overshootClamping = withDefault(config.overshootClamping, false);
        this._restDisplacementThreshold = withDefault(config.restDisplacementThreshold, 0.001);
        this._restSpeedThreshold = withDefault(config.restSpeedThreshold, 0.001);
        this._initialVelocity = config.velocity;
        this._lastVelocity = withDefault(config.velocity, 0);
        this._toValue = config.toValue;
        this.__isInteraction = config.isInteraction !== undefined ? config.isInteraction : true;

        let springConfig;
        if (config.bounciness !== undefined || config.speed !== undefined) {

            springConfig = SpringConfig.fromBouncinessAndSpeed(
                withDefault(config.bounciness, 8),
                withDefault(config.speed, 12),
            );
        } else {
            springConfig = SpringConfig.fromOrigamiTensionAndFriction(
                withDefault(config.tension, 40),
                withDefault(config.friction, 7),
            );
        }
        this._tension = springConfig.tension;
        this._friction = springConfig.friction;
    }

    start(
        fromValue,
        onUpdate,
        onEnd,
        previousAnimation
    ) {
        this.__active = true;
        this._startPosition = fromValue;
        this._lastPosition = this._startPosition;

        this._onUpdate = onUpdate;
        this.__onEnd = onEnd;
        this._lastTime = Date.now();

        if (previousAnimation instanceof SpringAnimation) {
            let internalState = previousAnimation.getInternalState();
            this._lastPosition = internalState.lastPosition;
            this._lastVelocity = internalState.lastVelocity;
            this._lastTime = internalState.lastTime;
        }
        if (this._initialVelocity !== undefined &&
            this._initialVelocity !== null) {
            this._lastVelocity = this._initialVelocity;
        }
        this.onUpdate();
    }

    getInternalState() {
        return {
            lastPosition: this._lastPosition,
            lastVelocity: this._lastVelocity,
            lastTime: this._lastTime,
        };
    }

    onUpdate() {
        let position = this._lastPosition;
        let velocity = this._lastVelocity;

        let tempPosition = this._lastPosition;
        let tempVelocity = this._lastVelocity;

        // If for some reason we lost a lot of frames (e.g. process large payload or
        // stopped in the debugger), we only advance by 4 frames worth of
        // computation and will continue on the next frame. It's better to have it
        // running at faster speed than jumping to the end.
        let MAX_STEPS = 64;
        let now = Date.now();
        if (now > this._lastTime + MAX_STEPS) {
            now = this._lastTime + MAX_STEPS;
        }

        // We are using a fixed time step and a maximum number of iterations.
        // The following post provides a lot of thoughts into how to build this
        // loop: http://gafferongames.com/game-physics/fix-your-timestep/
        let TIMESTEP_MSEC = 1;
        let numSteps = Math.floor((now - this._lastTime) / TIMESTEP_MSEC);

        for (let i = 0; i < numSteps; ++i) {
            // Velocity is based on seconds instead of milliseconds
            let step = TIMESTEP_MSEC / 1000;

            // This is using RK4. A good blog post to understand how it works:
            // http://gafferongames.com/game-physics/integration-basics/
            let aVelocity = velocity;
            let aAcceleration = this._tension * (this._toValue - tempPosition) - this._friction * tempVelocity;
            let tempPosition = position + aVelocity * step / 2;
            let tempVelocity = velocity + aAcceleration * step / 2;

            let bVelocity = tempVelocity;
            let bAcceleration = this._tension * (this._toValue - tempPosition) - this._friction * tempVelocity;
            tempPosition = position + bVelocity * step / 2;
            tempVelocity = velocity + bAcceleration * step / 2;

            let cVelocity = tempVelocity;
            let cAcceleration = this._tension * (this._toValue - tempPosition) - this._friction * tempVelocity;
            tempPosition = position + cVelocity * step / 2;
            tempVelocity = velocity + cAcceleration * step / 2;

            let dVelocity = tempVelocity;
            let dAcceleration = this._tension * (this._toValue - tempPosition) - this._friction * tempVelocity;
            tempPosition = position + cVelocity * step / 2;
            tempVelocity = velocity + cAcceleration * step / 2;

            let dxdt = (aVelocity + 2 * (bVelocity + cVelocity) + dVelocity) / 6;
            let dvdt = (aAcceleration + 2 * (bAcceleration + cAcceleration) + dAcceleration) / 6;

            position += dxdt * step;
            velocity += dvdt * step;
        }

        this._lastTime = now;
        this._lastPosition = position;
        this._lastVelocity = velocity;

        this._onUpdate(position);
        if (!this.__active) { // a listener might have stopped us in _onUpdate
            return;
        }

        // Conditions for stopping the spring animation
        let isOvershooting = false;
        if (this._overshootClamping && this._tension !== 0) {
            if (this._startPosition < this._toValue) {
                isOvershooting = position > this._toValue;
            } else {
                isOvershooting = position < this._toValue;
            }
        }
        let isVelocity = Math.abs(velocity) <= this._restSpeedThreshold;
        let isDisplacement = true;
        if (this._tension !== 0) {
            isDisplacement = Math.abs(this._toValue - position) <= this._restDisplacementThreshold;
        }

        if (isOvershooting || (isVelocity && isDisplacement)) {
            if (this._tension !== 0) {
                // Ensure that we end up with a round value
                this._onUpdate(this._toValue);
            }

            this.__debouncedOnEnd({finished: true});
            return;
        }
        this._animationFrame = requestAnimationFrame(this.onUpdate.bind(this));
    }

    stop() {
        this.__active = false;
        global.cancelAnimationFrame(this._animationFrame);
        this.__debouncedOnEnd({finished: false});
    }
}

// type ValueListenerCallback = (state: {value: number}) => void;

let _uniqueId = 1;

/**
 * Standard value for driving animations.  One `Animated.Value` can drive
 * multiple properties in a synchronized fashion, but can only be driven by one
 * mechanism at a time.  Using a new mechanism (e.g. starting a new animation,
 * or calling `setValue`) will stop any previous ones.
 */
class AnimatedValue extends AnimatedWithChildren {
    _value;
    _offset;
    _animation;
    _tracking;
    _listeners;

    constructor(value) {
        super();
        this._value = value;
        this._offset = 0;
        this._animation = null;
        this._listeners = {};
    }

    __detach() {
        this.stopAnimation();
    }

    __getValue() {
        return this._value + this._offset;
    }

    /**
     * Directly set the value.  This will stop any animations running on the value
     * and update all the bound properties.
     */
    setValue(value) {
        if (this._animation) {
            this._animation.stop();
            this._animation = null;
        }
        this._updateValue(value);
    }

    /**
     * Sets an offset that is applied on top of whatever value is set, whether via
     * `setValue`, an animation, or `Animated.event`.  Useful for compensating
     * things like the start of a pan gesture.
     */
    setOffset(offset) {
        this._offset = offset;
    }

    /**
     * Merges the offset value into the base value and resets the offset to zero.
     * The final output of the value is unchanged.
     */
    flattenOffset() {
        this._value += this._offset;
        this._offset = 0;
    }

    /**
     * Adds an asynchronous listener to the value so you can observe updates from
     * animations.  This is useful because there is no way to
     * synchronously read the value because it might be driven natively.
     */
    addListener(callback) {
        let id = String(_uniqueId++);
        this._listeners[id] = callback;
        return id;
    }

    removeListener(id) {
        delete this._listeners[id];
    }

    removeAllListeners() {
        this._listeners = {};
    }

    /**
     * Stops any running animation or tracking.  `callback` is invoked with the
     * final value after stopping the animation, which is useful for updating
     * state to match the animation position with layout.
     */
    stopAnimation(callback) {
        this.stopTracking();
        this._animation && this._animation.stop();
        this._animation = null;
        callback && callback(this.__getValue());
    }

    /**
     * Interpolates the value before updating the property, e.g. mapping 0-1 to
     * 0-10.
     */
    interpolate(config) {
        return new AnimatedInterpolation(this, config);
    }

    /**
     * Typically only used internally, but could be used by a custom Animation
     * class.
     */
    animate(animation, callback) {
        let previousAnimation = this._animation;
        this._animation && this._animation.stop();
        this._animation = animation;
        animation.start(
            this._value,
            (value) => {
                this._updateValue(value);
            },
            (result) => {
                this._animation = null;
                callback && callback(result);
            },
            previousAnimation,
        );
    }

    /**
     * Typically only used internally.
     */
    stopTracking() {
        this._tracking && this._tracking.__detach();
        this._tracking = null;
    }

    /**
     * Typically only used internally.
     */
    track(tracking) {
        this.stopTracking();
        this._tracking = tracking;
    }

    _updateValue(value) {
        this._value = value;
        _flush(this);
        for (let key in this._listeners) {
            this._listeners[key]({value: this.__getValue()});
        }
    }
}

// type ValueXYListenerCallback = (value: {x: number; y: number}) => void;

/**
 * 2D Value for driving 2D animations, such as pan gestures.  Almost identical
 * API to normal `Animated.Value`, but multiplexed.  Contains two regular
 * `Animated.Value`s under the hood.  Example:
 *
 *```javascript
 *  class DraggableView extends React.Component {
 *    constructor(props) {
 *      super(props);
 *      this.state = {
 *        pan: new Animated.ValueXY(), // inits to zero
 *      };
 *      this.state.panResponder = PanResponder.create({
 *        onStartShouldSetPanResponder: () => true,
 *        onPanResponderMove: Animated.event([null, {
 *          dx: this.state.pan.x, // x,y are Animated.Value
 *          dy: this.state.pan.y,
 *        }]),
 *        onPanResponderRelease: () => {
 *          Animated.spring(
 *            this.state.pan,         // Auto-multiplexed
 *            {toValue: {x: 0, y: 0}} // Back to zero
 *          ).start();
 *        },
 *      });
 *    }
 *    render() {
 *      return (
 *        <Animated.View
 *          {...this.state.panResponder.panHandlers}
 *          style={this.state.pan.getLayout()}>
 *          {this.props.children}
 *        </Animated.View>
 *      );
 *    }
 *  }
 *```
 */
class AnimatedValueXY extends AnimatedWithChildren {
    x;
    y;
    _listeners;

    constructor(valueIn) {
        super();
        let value = valueIn || {x: 0, y: 0};  // @flowfixme: shouldn't need `: any`
        if (typeof value.x === 'number' && typeof value.y === 'number') {
            this.x = new AnimatedValue(value.x);
            this.y = new AnimatedValue(value.y);
        } else {
            this.x = value.x;
            this.y = value.y;
        }
        this._listeners = {};
    }

    setValue(value) {
        this.x.setValue(value.x);
        this.y.setValue(value.y);
    }

    setOffset(offset) {
        this.x.setOffset(offset.x);
        this.y.setOffset(offset.y);
    }

    flattenOffset(){
        this.x.flattenOffset();
        this.y.flattenOffset();
    }

    __getValue() {
        return {
            x: this.x.__getValue(),
            y: this.y.__getValue(),
        };
    }

    stopAnimation(callback){
        this.x.stopAnimation();
        this.y.stopAnimation();
        callback && callback(this.__getValue());
    }

    addListener(callback) {
        let id = String(_uniqueId++);
        let jointCallback = ({value}) => {
            callback(this.__getValue());
        };
        this._listeners[id] = {
            x: this.x.addListener(jointCallback),
            y: this.y.addListener(jointCallback),
        };
        return id;
    }

    removeListener(id) {
        this.x.removeListener(this._listeners[id].x);
        this.y.removeListener(this._listeners[id].y);
        delete this._listeners[id];
    }

    /**
     * Converts `{x, y}` into `{left, top}` for use in style, e.g.
     *
     *```javascript
     *  style={this.state.anim.getLayout()}
     *```
     */
    getLayout() {
        return {
            left: this.x,
            top: this.y,
        };
    }

    /**
     * Converts `{x, y}` into a useable translation transform, e.g.
     *
     *```javascript
     *  style={{
   *    transform: this.state.anim.getTranslateTransform()
   *  }}
     *```
     */
    getTranslateTransform() {
        return [
            {translateX: this.x},
            {translateY: this.y}
        ];
    }
}

class AnimatedInterpolation extends AnimatedWithChildren {
    _parent;
    _interpolation;

    constructor(parent, config) {
        super();
        this._parent = parent;
        this._interpolation = Interpolation.createInterpolation(config);
    }

    __getValue() {
        let parentValue = this._parent.__getValue();

        return this._interpolation(parentValue);
    }

    interpolate(config) {
        return new AnimatedInterpolation(this, Interpolation.create(config));
    }

    __attach() {
        this._parent.__addChild(this);
    }

    __detach() {
        this._parent.__removeChild(this);
    }
}

class AnimatedAddition extends AnimatedWithChildren {
    _a;
    _b;

    constructor(a, b) {
        super();
        this._a = a;
        this._b = b;
    }

    __getValue() {
        return this._a.__getValue() + this._b.__getValue();
    }

    interpolate(config) {
        return new AnimatedInterpolation(this, config);
    }

    __attach() {
        this._a.__addChild(this);
        this._b.__addChild(this);
    }

    __detach() {
        this._a.__removeChild(this);
        this._b.__removeChild(this);
    }
}

class AnimatedMultiplication extends AnimatedWithChildren {
    _a;
    _b;

    constructor(a, b) {
        super();
        this._a = a;
        this._b = b;
    }

    __getValue() {
        return this._a.__getValue() * this._b.__getValue();
    }

    interpolate(config) {
        return new AnimatedInterpolation(this, config);
    }

    __attach() {
        this._a.__addChild(this);
        this._b.__addChild(this);
    }

    __detach() {
        this._a.__removeChild(this);
        this._b.__removeChild(this);
    }
}


class AnimatedModulo extends AnimatedWithChildren {
    _a;
    _modulus;

    constructor(a, modulus) {
        super();
        this._a = a;
        this._modulus = modulus;
    }

    __getValue() {
        return (this._a.__getValue() % this._modulus + this._modulus) % this._modulus;
    }

    interpolate(config) {
        return new AnimatedInterpolation(this, config);
    }

    __attach() {
        this._a.__addChild(this);
    }

    __detach() {
        this._a.__removeChild(this);
    }
}

class AnimatedTransform extends AnimatedWithChildren {
    _transforms;

    constructor(transforms) {
        super();
        this._transforms = transforms;
    }

    __getValue() {
        if('string'===typeof this._transforms){
            return this._transforms;
        }

        return this._transforms.map(transform => {
            let result = {};
            for (let key in transform) {
                let value = transform[key];
                if (value instanceof Animated) {
                    result[key] = value.__getValue();
                } else {
                    result[key] = value;
                }
            }
            return result;
        });
    }

    __getAnimatedValue() {
        return this._transforms.map(transform => {
            let result = {};
            for (let key in transform) {
                let value = transform[key];
                if (value instanceof Animated) {
                    result[key] = value.__getAnimatedValue();
                } else {
                    // All transform components needed to recompose matrix
                    result[key] = value;
                }
            }
            return result;
        });
    }

    __attach() {
        this._transforms&&this._transforms.forEach&&this._transforms.forEach(transform => {
            for (let key in transform) {
                let value = transform[key];
                if (value instanceof Animated) {
                    value.__addChild(this);
                }
            }
        });
    }

    __detach() {
        this._transforms&&this._transforms.forEach&&this._transforms.forEach(transform => {
            for (let key in transform) {
                let value = transform[key];
                if (value instanceof Animated) {
                    value.__removeChild(this);
                }
            }
        });
    }
}

class AnimatedStyle extends AnimatedWithChildren {
    _style;

    constructor(style) {
        super();
        style = flatten(style) || {};
        if (style.transform) {
            style = {
                ...style,
                transform: new AnimatedTransform(style.transform),
            };
        }
        this._style = style;
    }

    __getValue() {
        let style = {};
        for (let key in this._style) {
            let value = this._style[key];
            if (value instanceof Animated) {
                style[key] = value.__getValue();
            } else {
                style[key] = value;
            }

        }

        return style;
    }

    __getAnimatedValue() {
        let style = {};
        for (let key in this._style) {
            let value = this._style[key];
            if (value instanceof Animated) {
                style[key] = value.__getAnimatedValue();
            }
        }
        return style;
    }

    __attach() {
        for (let key in this._style) {
            let value = this._style[key];
            if (value instanceof Animated) {
                value.__addChild(this);
            }
        }
    }

    __detach() {
        for (let key in this._style) {
            let value = this._style[key];
            if (value instanceof Animated) {
                value.__removeChild(this);
            }
        }
    }
}

class AnimatedProps extends Animated {
    _props;
    _callback;

    constructor(
        props,
        callback
    ) {
        super();
        if (props.style) {
            props = {
                ...props,
                style: new AnimatedStyle(props.style),
            };
        }
        this._props = props;
        this._callback = callback;
        this.__attach();
    }

    __getValue() {
        let props = {};
        for (let key in this._props) {
            let value = this._props[key];
            if (value instanceof Animated) {
                props[key] = value.__getValue();
            } else {
                props[key] = value;
            }
        }
        return props;
    }

    __getAnimatedValue() {
        let props = {};
        for (let key in this._props) {
            let value = this._props[key];
            if (value instanceof Animated) {
                props[key] = value.__getAnimatedValue();
            }
        }
        return props;
    }

    __attach() {
        for (let key in this._props) {
            let value = this._props[key];
            if (value instanceof Animated) {
                value.__addChild(this);
            }
        }
    }

    __detach() {
        for (let key in this._props) {
            let value = this._props[key];
            if (value instanceof Animated) {
                value.__removeChild(this);
            }
        }
    }

    update() {
        this._callback();
    }
}

function createAnimatedComponent(Component) {

    class AnimatedComponent extends React.Component {
        constructor(props) {
            super(props);
        }
        _propsAnimated;

        componentWillUnmount() {
            this._propsAnimated && this._propsAnimated.__detach();
        }

        setNativeProps(props) {

            this.instanceOfComponent&&this.instanceOfComponent.setNativeProps(props);
        }

        componentWillMount() {
            this.attachProps(this.props);

        }

        attachProps(nextProps) {
            let oldPropsAnimated = this._propsAnimated;

            // The system is best designed when setNativeProps is implemented. It is
            // able to avoid re-rendering and directly set the attributes that
            // changed. However, setNativeProps can only be implemented on leaf
            // native components. If you want to animate a composite component, you
            // need to re-render it. In this case, we have a fallback that uses
            // forceUpdate.
            let callback = () => {
                if (this.instanceOfComponent&&this.instanceOfComponent.setNativeProps) {
                    let value = this._propsAnimated.__getAnimatedValue();
                    //fix shadowOpacity bug
                    if(value.style&&!isNaN(value.style.shadowOpacity)){
                        let value2=this._propsAnimated.__getValue();
                        value.style=Object.assign(value2.style,value.style);
                        value.style.hasFixed=false;
                    }
                    this.instanceOfComponent.setNativeProps(value);
                } else {
                    this.forceUpdate();
                }

            };

            this._propsAnimated = new AnimatedProps(
                nextProps,
                callback,
            );

            // When you call detach, it removes the element from the parent list
            // of children. If it goes to 0, then the parent also detaches itself
            // and so on.
            // An optimization is to attach the new elements and THEN detach the old
            // ones instead of detaching and THEN attaching.
            // This way the intermediate state isn't to go to 0 and trigger
            // this expensive recursive detaching to then re-attach everything on
            // the very next operation.
            oldPropsAnimated && oldPropsAnimated.__detach();
        }

        componentWillReceiveProps(nextProps) {
            this.attachProps(nextProps);
        }

        render() {
            let value = this._propsAnimated.__getValue();
            if(value.style){
                value.style=fix(value.style);
            }
            value.className = 'animationComponent ' + (value.className || '')
            return (
                <Component
                    {...value}
                    ref={(ins)=>{this.instanceOfComponent=ins}}
                />
            );
        }

        // A third party library can use getNode()
        // to get the node reference of the decorated component
        getNode() {
            return this.instanceOfComponent;
        }
    }
    AnimatedComponent.propTypes = {
    };

    return AnimatedComponent;
}

class AnimatedTracking extends Animated {
    _value;
    _parent;
    _callback;
    _animationConfig;
    _animationClass;

    constructor(
        value,
        parent,
        animationClass,
        animationConfig,
        callback
    ) {
        super();
        this._value = value;
        this._parent = parent;
        this._animationClass = animationClass;
        this._animationConfig = animationConfig;
        this._callback = callback;
        this.__attach();
    }

    __getValue() {
        return this._parent.__getValue();
    }

    __attach() {
        this._parent.__addChild(this);
    }

    __detach() {
        this._parent.__removeChild(this);
    }

    update() {
        this._value.animate(new this._animationClass({
            ...this._animationConfig,
            toValue: (this._animationConfig.toValue).__getValue(),
        }), this._callback);
    }
}
