let maybeVectorAnim = function(
    value,
    config,
    anim
) {
    if (value instanceof AnimatedValueXY) {
        let configX = {...config};
        let configY = {...config};
        for (let key in config) {
            let {x, y} = config[key];
            if (x !== undefined && y !== undefined) {
                configX[key] = x;
                configY[key] = y;
            }
        }
        let aX = anim((value).x, configX);
        let aY = anim((value).y, configY);
        // We use `stopTogether: false` here because otherwise tracking will break
        // because the second animation will get stopped before it can update.
        return parallel([aX, aY], {stopTogether: false});
    }
    return null;
};

let spring = function(
    value,
    config
) {
    return maybeVectorAnim(value, config, spring) || {
        start: function(callback) {
            let singleValue = value;
            let singleConfig = config;
            singleValue.stopTracking();
            if (config.toValue instanceof Animated) {
                singleValue.track(new AnimatedTracking(
                    singleValue,
                    config.toValue,
                    SpringAnimation,
                    singleConfig,
                    callback
                ));
            } else {
                singleValue.animate(new SpringAnimation(singleConfig), callback);
            }
        },

        stop: function() {
            value.stopAnimation();
        },
    };
};

let timing = function(
    value,
    config,
) {
    return maybeVectorAnim(value, config, timing) || {
        start: function(callback) {
            let singleValue = value;
            let singleConfig = config;
            singleValue.stopTracking();
            if (config.toValue instanceof Animated) {
                singleValue.track(new AnimatedTracking(
                    singleValue,
                    config.toValue,
                    TimingAnimation,
                    singleConfig,
                    callback
                ));
            } else {
                singleValue.animate(new TimingAnimation(singleConfig), callback);
            }
        },

        stop: function() {
            value.stopAnimation();
        },
    };
};

let decay = function(
    value,
    config,
) {
    return maybeVectorAnim(value, config, decay) || {
        start: function(callback) {
            let singleValue = value;
            let singleConfig = config;
            singleValue.stopTracking();
            singleValue.animate(new DecayAnimation(singleConfig), callback);
        },

        stop: function() {
            value.stopAnimation();
        },
    };
};

let sequence = function(
    animations
) {
    let current = 0;
    return {
        start: function(callback) {
            let onComplete = function(result) {
                if (!result.finished) {
                    callback && callback(result);
                    return;
                }

                current++;

                if (current === animations.length) {
                    callback && callback(result);
                    return;
                }

                animations[current].start(onComplete);
            };

            if (animations.length === 0) {
                callback && callback({finished: true});
            } else {
                animations[current].start(onComplete);
            }
        },

        stop: function() {
            if (current < animations.length) {
                animations[current].stop();
            }
        }
    };
};

// type ParallelConfig = {
//   stopTogether?: bool; // If one is stopped, stop all.  default: true
// }
let parallel = function(
    animations,
    config
) {
    let doneCount = 0;
    // Make sure we only call stop() at most once for each animation
    let hasEnded = {};
    let stopTogether = !(config && config.stopTogether === false);

    let result = {
        start: function(callback) {
            if (doneCount === animations.length) {
                callback && callback({finished: true});
                return;
            }

            animations.forEach((animation, idx) => {
                let cb = function(endResult) {
                    hasEnded[idx] = true;
                    doneCount++;
                    if (doneCount === animations.length) {
                        doneCount = 0;
                        callback && callback(endResult);
                        return;
                    }

                    if (!endResult.finished && stopTogether) {
                        result.stop();
                    }
                };

                if (!animation) {
                    cb({finished: true});
                } else {
                    animation.start(cb);
                }
            });
        },

        stop: function() {
            animations.forEach((animation, idx) => {
                !hasEnded[idx] && animation.stop();
                hasEnded[idx] = true;
            });
        }
    };

    return result;
};

let delay = function(time) {
    // Would be nice to make a specialized implementation
    return timing(new AnimatedValue(0), {toValue: 0, delay: time, duration: 0});
};

let stagger = function(
    time,
    animations
) {
    return parallel(animations.map((animation, i) => {
        return sequence([
            delay(time * i),
            animation,
        ]);
    }));
};

